<template>
  <div>
    <div style="border-bottom: 1px solid #eee; padding-bottom: 20px; margin-bottom: 20px;">
      <h2 style="margin: 0; display: inline; margin-right: 10px;">{{ teamName }}</h2>
      <el-button type="text" @click="editTeamName"></el-button>
    </div>
    <div class="operation-container">
      <h3 style="margin: 0;"></h3>
      <p style="margin: 0;"></p>
      <el-button @click="deleteTeam" class="operation-button" size="small" type="danger" plain></el-button>
    </div>
  </div>
</template>

<script>
import * as service from '../service';
import router from '../router';

export default {
  name: 'Settings',
  props: ['teamName', 'changeTeamName'],
  methods: {
    editTeamName() {
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        inputValue: this.teamName,
        inputPattern: /.+/,
        inputErrorMessage: '',
      }).then(({ value }) => {
        service.setName(this.$route.params.tid, value).then((data) => {
          if (data.error) {
            throw Error(data.error);
          }
          this.changeTeamName(value);
        }).catch((err) => {
          this.$message.error(err.message);
        });
      }).catch(() => { });
    },
    deleteTeam() {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      }).then(() => {
        service.deleteTeam(this.$route.params.tid).then((data) => {
          if (data.error) {
            throw Error(data.error);
          }
          this.$message({
            type: 'success',
            message: '!',
          });
          router.push({ name: 'Launchpad' });
        }).catch((err) => {
          this.$message.error(err.message);
        });
      }).catch(() => { });
    },
  },
};
</script>

<style lang="scss">
.operation-container {
  position: relative;
}
.operation-button {
  position: absolute;
  top: 50%;
  margin-top: -15px;
  right: 0px;
}
</style>



